.contact {
  background-color: #f9f9f9;
  padding: 100px 0;
}

.contact-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
  margin-top: 40px;
}

.contact-info {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.contact-item {
  display: flex;
  align-items: flex-start;
  background-color: #fff;
  padding: 25px;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease;
}

.contact-item:hover {
  transform: translateY(-5px);
}

.contact-icon {
  font-size: 2rem;
  color: #4fb3bf;
  margin-right: 20px;
  flex-shrink: 0;
}

.contact-details h3 {
  font-size: 1.3rem;
  margin-bottom: 10px;
  color: #1a1a1a;
}

.contact-details p {
  font-size: 1rem;
  color: #555;
}

.contact-details a {
  color: #4fb3bf;
  text-decoration: none;
  transition: color 0.3s ease;
}

.contact-details a:hover {
  color: #389aa5;
  text-decoration: underline;
}

.contact-form-container {
  background-color: #fff;
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.08);
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  font-size: 1rem;
  margin-bottom: 8px;
  color: #1a1a1a;
  font-weight: 500;
}

.form-group input, 
.form-group textarea {
  padding: 12px 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
  font-family: inherit;
}

.form-group input:focus, 
.form-group textarea:focus {
  outline: none;
  border-color: #4fb3bf;
}

.submit-btn {
  background-color: #4fb3bf;
  color: #fff;
  padding: 12px 25px;
  border: none;
  border-radius: 5px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: 10px;
  align-self: flex-start;
}

.submit-btn:hover {
  background-color: #389aa5;
}

.form-success-message {
  background-color: #dff0d8;
  color: #3c763d;
  padding: 15px;
  border-radius: 5px;
  margin-top: 20px;
  font-weight: 500;
  text-align: center;
}

@media screen and (max-width: 992px) {
  .contact-content {
    grid-template-columns: 1fr;
    gap: 40px;
  }
  
  .contact-info {
    order: 2;
  }
  
  .contact-form-container {
    order: 1;
  }
}

@media screen and (max-width: 768px) {
  .contact {
    padding: 70px 0;
  }
  
  .contact-item {
    padding: 20px;
  }
  
  .contact-icon {
    font-size: 1.5rem;
    margin-right: 15px;
  }
  
  .contact-details h3 {
    font-size: 1.1rem;
  }
  
  .contact-details p {
    font-size: 0.9rem;
  }
  
  .submit-btn {
    padding: 10px 20px;
    font-size: 1rem;
  }
} 