.skills {
  background-color: var(--light-bg);
  padding: var(--spacing-xl) 0;
  position: relative;
  overflow: hidden;
}

.skills::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 5px;
  background: var(--primary-gradient);
}

.section-description {
  text-align: center;
  max-width: 700px;
  margin: 0 auto var(--spacing-xl);
  color: var(--text-medium);
  font-size: 1.1rem;
  line-height: 1.6;
}

.skills-content {
  display: flex;
  flex-wrap: wrap;
  gap: var(--spacing-lg);
  justify-content: space-between;
}

.skill-category {
  flex: 1;
  min-width: 300px;
  background-color: var(--white-bg);
  border-radius: var(--border-radius-lg);
  padding: 35px;
  box-shadow: var(--shadow-lg);
  transition: var(--transition-normal);
  height: 100%;
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.05);
}

.skill-category:hover {
  transform: translateY(-12px);
  box-shadow: var(--shadow-xl);
  border-color: rgba(79, 179, 191, 0.2);
}

.category-header {
  display: flex;
  align-items: center;
  margin-bottom: var(--spacing-md);
}

.category-icon {
  font-size: 2rem;
  color: var(--primary-color);
  margin-right: var(--spacing-md);
  background-color: rgba(79, 179, 191, 0.1);
  width: 50px;
  height: 50px;
  border-radius: var(--border-radius-circle);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.category-title {
  font-size: 1.8rem;
  color: var(--text-dark);
  margin: 0;
  position: relative;
}

.category-description {
  color: var(--text-medium);
  font-size: 1rem;
  margin-bottom: var(--spacing-md);
  line-height: 1.6;
}

.skill-bars {
  margin-top: var(--spacing-lg);
}

.skill-item-container {
  margin-bottom: var(--spacing-md);
  padding-bottom: var(--spacing-md);
  border-bottom: 1px dashed rgba(0, 0, 0, 0.05);
}

.skill-item-container:last-child {
  margin-bottom: 0;
  padding-bottom: 0;
  border-bottom: none;
}

.skill-name-with-icon {
  display: flex;
  align-items: center;
}

.skill-icon {
  margin-right: var(--spacing-sm);
  color: var(--primary-color);
  font-size: 1.2rem;
}

.skill-name {
  font-size: 1.1rem;
  font-weight: 500;
  color: var(--text-dark);
}

.skill-percentage {
  font-weight: 600;
  color: var(--primary-color);
}

.skill-level-indicator {
  font-size: 0.8rem;
  color: var(--text-muted);
  text-align: right;
  margin-top: var(--spacing-xs);
  font-style: italic;
}

/* Retain old styles for compatibility but hide them */
.skill-items {
  display: none;
}

.skill-item {
  display: none;
}

/* Responsive Styles */
@media screen and (max-width: 992px) {
  .skills-content {
    gap: var(--spacing-md);
  }
  
  .skill-category {
    min-width: 280px;
    padding: var(--spacing-md);
  }
  
  .category-icon {
    font-size: 1.8rem;
    width: 45px;
    height: 45px;
  }
  
  .category-title {
    font-size: 1.6rem;
  }
}

@media screen and (max-width: 768px) {
  .skills {
    padding: 70px 0;
  }
  
  .section-description {
    font-size: 1rem;
    margin-bottom: var(--spacing-lg);
  }
  
  .skills-content {
    flex-direction: column;
    gap: var(--spacing-md);
  }
  
  .skill-category {
    width: 100%;
    padding: var(--spacing-md);
  }
  
  .category-icon {
    font-size: 1.5rem;
    width: 40px;
    height: 40px;
    margin-right: var(--spacing-sm);
  }
  
  .category-title {
    font-size: 1.4rem;
  }
  
  .skill-name {
    font-size: 1rem;
  }
}

.hexagon-skills-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 40px 0 60px;
  padding: 20px;
  background-color: var(--white-bg);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-md);
  position: relative;
}

.hexagon-skills-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(79, 179, 191, 0.05) 0%, rgba(166, 225, 224, 0.05) 100%);
  border-radius: var(--border-radius-lg);
  z-index: 0;
}

@media screen and (max-width: 768px) {
  .hexagon-skills-container {
    margin: 30px 0 40px;
    padding: 15px;
  }
} 