.achievements {
  background-color: #fff;
  padding: 100px 0;
}

.achievements-content {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(500px, 1fr));
  gap: 30px;
}

.achievement-category {
  background-color: #f9f9f9;
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease;
}

.achievement-category:hover {
  transform: translateY(-10px);
}

.category-header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.category-icon {
  background-color: #4fb3bf;
  color: #fff;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  margin-right: 15px;
}

.category-title {
  font-size: 1.5rem;
  color: #1a1a1a;
}

.achievement-list {
  padding-left: 20px;
}

.achievement-item {
  position: relative;
  padding: 10px 0;
  font-size: 1rem;
  color: #555;
  line-height: 1.6;
}

.achievement-item:not(:last-child) {
  border-bottom: 1px dashed #ddd;
}

@media screen and (max-width: 768px) {
  .achievements {
    padding: 70px 0;
  }
  
  .achievements-content {
    grid-template-columns: 1fr;
  }
  
  .category-title {
    font-size: 1.3rem;
  }
  
  .achievement-item {
    font-size: 0.95rem;
  }
}

@media screen and (max-width: 576px) {
  .achievements-content {
    gap: 20px;
  }
  
  .achievement-category {
    padding: 20px;
  }
  
  .category-icon {
    width: 40px;
    height: 40px;
    font-size: 1.2rem;
  }
} 