.projects {
  background-color: var(--light-bg);
  padding: var(--spacing-xl) 0;
  position: relative;
  overflow: hidden;
}

.projects::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at top right, rgba(79, 179, 191, 0.05) 0%, rgba(255, 255, 255, 0) 70%);
  pointer-events: none;
}

.section-subtitle {
  text-align: center;
  color: var(--primary-color);
  font-size: 1.2rem;
  font-weight: 500;
  margin-bottom: var(--spacing-sm);
  letter-spacing: 2px;
  text-transform: uppercase;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 40px;
  margin-top: var(--spacing-xl);
  width: 100%;
}

.project-card {
  background-color: var(--white-bg);
  border-radius: var(--border-radius-lg);
  overflow: hidden;
  box-shadow: var(--shadow-md);
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  height: 100%;
  position: relative;
  z-index: 1;
  border: 1px solid rgba(0, 0, 0, 0.05);
  display: flex;
  flex-direction: column;
}

.project-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 5px;
  background: var(--primary-gradient);
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.4s ease;
  z-index: 1;
}

.project-card:hover {
  transform: translateY(-15px);
  box-shadow: var(--shadow-xl);
}

.project-card:hover::before {
  transform: scaleX(1);
}

.project-content {
  padding: 30px;
  display: flex;
  flex-direction: column;
  height: 100%;
  position: relative;
  flex: 1;
}

.project-title {
  font-size: 1.7rem;
  margin-bottom: 10px;
  color: var(--text-dark);
  position: relative;
  display: inline-block;
}

.project-date {
  font-size: 0.9rem;
  color: var(--text-muted);
  margin-bottom: 20px;
  display: flex;
  align-items: center;
}

.project-date::before {
  content: '';
  display: inline-block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: var(--primary-color);
  margin-right: 8px;
}

.project-description {
  font-size: 1rem;
  line-height: 1.6;
  color: var(--text-medium);
  margin-bottom: 25px;
  flex-grow: 1;
}

.project-tech {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 25px;
}

.tech-tag {
  background-color: rgba(79, 179, 191, 0.1);
  color: var(--primary-color);
  font-size: 0.85rem;
  font-weight: 600;
  padding: 6px 14px;
  border-radius: 20px;
  transition: all 0.3s ease;
}

.project-card:hover .tech-tag {
  background-color: rgba(79, 179, 191, 0.2);
}

.project-points {
  padding-left: 25px;
  margin-bottom: 25px;
}

.project-points li {
  font-size: 0.95rem;
  color: var(--text-medium);
  margin-bottom: 12px;
  line-height: 1.5;
  position: relative;
}

.project-points li::before {
  content: 'â–¹';
  position: absolute;
  left: -20px;
  color: var(--primary-color);
  font-size: 1.2rem;
}

.project-links {
  display: flex;
  gap: 15px;
  margin-top: auto;
}

.project-link {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 20px;
  border-radius: 50px;
  text-decoration: none;
  font-size: 0.95rem;
  font-weight: 600;
  transition: all 0.3s ease;
}

.project-link:first-child {
  background-color: transparent;
  color: var(--primary-color);
  border: 2px solid var(--primary-color);
}

.project-link:last-child {
  background-color: var(--primary-color);
  color: white;
  border: 2px solid var(--primary-color);
}

.project-link:first-child:hover {
  background-color: var(--primary-color);
  color: white;
  transform: translateY(-3px);
  box-shadow: 0 5px 15px rgba(79, 179, 191, 0.3);
}

.project-link:last-child:hover {
  background-color: var(--primary-dark);
  border-color: var(--primary-dark);
  transform: translateY(-3px);
  box-shadow: 0 5px 15px rgba(79, 179, 191, 0.3);
}

@media screen and (max-width: 992px) {
  .projects-grid {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 30px;
  }
  
  .project-content {
    padding: 25px;
  }
  
  .project-title {
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 768px) {
  .projects {
    padding: 70px 0;
  }
  
  .projects-grid {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  
  .project-title {
    font-size: 1.4rem;
  }
  
  .project-content {
    padding: 20px;
  }
}

@media screen and (max-width: 480px) {
  .projects-grid {
    grid-template-columns: 1fr;
  }
} 